.button {
  position: absolute;
  top: 50%;
  width: 50px;
}

.buttonCircle {
  stroke-width: 0.5;
}

.buttonDirectional {
  left: calc((100% - 20px) / 2);
}

.buttonNonDirectional {
  left: 50%;
}

.buttonUp {
  transform-origin: top;
  transform: rotate(90deg);
}

.buttonDown {
  transform-origin: left;
  transform: rotate(-90deg);
}

.buttonLeft {
  transform: rotate(180deg) translate(50%, 50%);
}

.buttonRight {
  transform: translate(-50%, -50%);
}

.editHoverButton {
  display: none;
}
